"use client";

import { useState } from "react";
import { Input } from "@/components/ui/input";
import { Search, Bot } from "lucide-react";

interface SearchResult {
  title: string;
  type: string;
  size: string;
}

export function AISearch() {
  const [isSearching, setIsSearching] = useState(false);
  const [searchResults, setSearchResults] = useState<SearchResult[]>([]);
  const [query, setQuery] = useState("");
  const [showResults, setShowResults] = useState(false);

  const handleSearch = async () => {
    if (!query.trim()) return;
    
    setIsSearching(true);
    setShowResults(true);
    // Simulate AI search results
    setTimeout(() => {
      setSearchResults([
        { title: "Project Report.pdf", type: "Document", size: "2.3 MB" },
        { title: "Vacation Photos", type: "Photos", size: "156 MB" },
        { title: "Meeting Recording.mp4", type: "Video", size: "45 MB" },
      ]);
      setIsSearching(false);
    }, 1000);
  };

  return (
    <div className="space-y-4">
      <div className="relative">
        <Search className="absolute left-4 top-3.5 h-5 w-5 text-gray-400" />
        <Input
          placeholder="Search with BlackBox AI..."
          className="pl-12 pr-12 py-6 bg-gray-50 border-none rounded-2xl text-base shadow-sm focus-visible:ring-1 focus-visible:ring-gray-200"
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          onKeyDown={(e) => e.key === "Enter" && handleSearch()}
        />
        <button
          onClick={handleSearch}
          className="absolute right-4 top-3 text-purple-500 hover:text-purple-600 transition-colors"
        >
          <Bot className="h-6 w-6" />
        </button>
      </div>

      {/* Search Results Section */}
      {showResults && (
        <div className="bg-white rounded-2xl shadow-lg border border-gray-100 overflow-hidden">
          {isSearching ? (
            <div className="p-6 text-center text-gray-500">
              <div className="animate-spin inline-block w-6 h-6 border-2 border-current border-t-transparent rounded-full mb-2" />
              <p>BlackBox AI is searching...</p>
            </div>
          ) : (
            <div className="divide-y divide-gray-100">
              <div className="p-4 bg-purple-50">
                <p className="text-sm text-purple-700 font-medium">Search results for: "{query}"</p>
              </div>
              {searchResults.map((result, index) => (
                <div
                  key={index}
                  className="p-4 hover:bg-gray-50 cursor-pointer flex items-center justify-between"
                >
                  <div className="flex items-center space-x-3">
                    <div className="w-10 h-10 bg-gray-100 rounded-lg flex items-center justify-center text-xl">
                      {result.type === "Document" && "üìÑ"}
                      {result.type === "Photos" && "üì∏"}
                      {result.type === "Video" && "üé•"}
                    </div>
                    <div>
                      <p className="font-medium">{result.title}</p>
                      <p className="text-sm text-gray-500">{result.size}</p>
                    </div>
                  </div>
                  <span className="text-sm text-gray-400 font-medium">{result.type}</span>
                </div>
              ))}
            </div>
          )}
        </div>
      )}
    </div>
  );
}
